.header {
  position: fixed;
  z-index: 2;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  padding: 12px 50px;

  background-color: $primary-dark-color;
  opacity: 0.97;
}

.header__title {
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  font-family: 'Pacifico', cursive;
  font-size: 40px;
  color: #fff;
  opacity: 0.8;
  text-shadow: 3px 3px 6px rgba(63, 63, 63, 0.616), 1px 1px 1px rgba(63, 63, 63, 0.616);
}

.logo {
  color: #fff;
  opacity: 0.8;
  transform: scale(1);

  transition: color $animation-duration $timing-function,
    opacity $animation-duration $timing-function, transform $animation-duration $timing-function;
}

.logo:hover {
  color: $accent-hover-color;
  opacity: 1;
  transform: scale(1.03);
}
